:root {
  --primaryGreen: hsl(177, 100%, 33%);
  --greenDark: hsl(176, 100%, 10%);
  --greenLight: hsl(176, 50%, 73%);
  --primaryBlue: hsl(196, 100%, 33%);
  --primaryBlueDark: hsl(196, 100%, 10%);
  --menu-shadow: hsla(196, 100%, 10%, 60%);
  --menu-overlay: hsla(196, 100%, 33%, 40%);
  --darkBlue: hsl(196, 100%, 24%);
  --lightBlue: hsl(196, 49%, 73%);
  --primaryOrange: hsl(30, 92%, 54%);
  --darkOrange: hsl(30, 77%, 44%);
  --lightOrange: hsl(31, 91%, 82%);
  --hoverOrange: hsl(30, 77%, 34%);
  --lightGrey: hsl(0, 0%, 92%);
  --medGrey: hsl(0, 0%, 44%);
  --dkGrey: hsl(0, 0%, 25%);
  --black: hsl(0, 0%, 6%);
  --white: hsl(240, 100%, 99%);
  --white60: hsla(240, 100%, 99%, .85);
  --primary: hsl(196, 100%, 33%);
  --secondary: hsl(120, 33%, 30%);
  --ghost: hsl(240, 100%, 99%);
}

:root {
  --font-heading: "Roboto Slab", Helvetica, Arial, sans-serif; /* heading font */
  --font-text: "Lato", Helvetica, Arial, sans-serif; /* body font */
}

@font-face {
  font-family: "Lato";
  font-style: normal;
  font-weight: 400;
  src: url(Lato-Regular.ttf) format("ttf");
}
@font-face {
  font-family: "PTSerif";
  font-style: normal;
  font-weight: 400;
  src: url(PTSerif-Regular.ttf) format("ttf");
}
@font-face {
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 500 700;
  src: url(OpenSans-Italic-VariableFont_wdth,wght.ttf) format("ttf");
}
html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-text);
