.footer__social {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 1rem;
}
@media (max-width: 43.74875em) {
  .footer__social {
    display: flex;
    flex-direction: row;
    justify-content: center;
    width: 50%;
    align-items: center;
    margin: 0 auto;
  }
}
.footer a {
  color: var(--primaryBlueDark);
  text-decoration: none;
  cursor: pointer;
}
.footer a:hover {
  color: var(--hoverOrange);
  text-decoration: underline;
}
.footer .copyright {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0 20px;
}
.footer .copyright > p {
  font-size: 14px;
}

.services__twoCols {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}
@media (max-width: 43.74875em) {
  .services__twoCols {
    grid-template-columns: 1fr;
  }
}
.services__col1 {
  width: 100%;
}
.services__intro {
  height: 26.875rem;
}
@media (max-width: 43.74875em) {
  .services__intro {
    height: auto;
  }
}
.services__col2 {
  width: 100%;
}
.services__card {
  background-color: var(--darkBlue);
  padding: 0 20px 20px 20px;
}
.services__card img {
  margin: 0 auto 16px;
  height: auto;
}
.services__card h2 {
  font-size: 2.25rem;
  font-weight: 400;
  color: var(--white);
}
.services__card p {
  color: var(--white);
  line-height: 2;
  margin-bottom: 0;
}

.services__card:first-child {
  margin-bottom: 32px;
}